<!--回款结算-->
<template>
    <BoxTitle>
        <div class="box" v-for="k in [0,1]">
            <div class="print-btn">
                <el-button v-if="k" type="primary" v-print="'#table-content1'">打 印</el-button>
                <el-button v-else type="primary" v-print="'#table-content'">打 印</el-button>
                <el-button type="default" @click="html2Excel(k)">导 出</el-button>
            </div>
            <div :id="'table-content'+(k?k:'')" ref="dom">
                <div class="item">
                    <div class="scorll">
                        <table class="table">
                            <thead>
                                <tr class="noBorder head">
                                    <td style="text-align:center" colspan="15">{{tableTitle[k]}}</td>
                                </tr>
                                <tr class="noBorder">
                                    <td style="text-align:right" colspan="15">金额单位：元</td>
                                </tr>
                            </thead>
                            <tr>
                                <th>序号</th>
                                <th>业主单位</th>
                                <th v-for="th in heads">{{th}}月</th>
                                <th>合计</th>
                            </tr>
                            <tr v-for="(item,index) in list[k]">
                                <td>{{index + 1}}</td>
                                <td>{{item.kcShortName}}</td>
                                <td v-for="td in item.data">{{ formatMoneyAddComma(td)}}</td>
                                <td>{{formatMoneyAddComma(item.sum)}}</td>
                            </tr>
                            <tr>
                                <td></td>
                                <td>合计</td>
                                <td v-for="td in everyMonthSum[k]">{{ formatMoneyAddComma(td)}}</td>
                                <td>{{formatMoneyAddComma(allSum[k])}}</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
        </div>
    </BoxTitle>
</template>
<script>
    //组件
    import {
        mapState
    } from 'vuex'
    import BoxTitle from "@/components/BoxTitle.vue";
    import InfoBox from "@/components/InfoBox.vue";
    import {
        parseTime,
        getPrevMonth,
        formatNum,
        formatMoneyAddComma
    } from "@/utils/index.js"
    //接口
    import {
        getTzQuery
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";
    import Table2Excel from 'table2excel.js'

    export default {
        name: 'useEqManage',
        components: {
            BoxTitle,
            InfoBox
        },
        data() {
            return {
                tableTitle: ['西藏先锋绿能环保科技股份有限公司回款统计台账', '成都中石油昆仑能源有限公司回款统计台账'],
                heads: [],
                list: [],
                everyMonthSum: [],
                allSum: [],
            }
        },
        computed: {
            ...mapState({
                personName: state => state.user.nickName,
            }),

        },
        mounted() {
            this.$nextTick(() => {
                let style = document.createElement('style')
                style.setAttribute("type", "text/css");
                style.setAttribute("id", "tableStyle");
                style.innerHTML = `
                <!--
                    @page {
                        margin:1.54cm 2cm;               
                    }
                -->`;
                this.$refs.dom[0].appendChild(style);
                this.$refs.dom[1].appendChild(style);
            })
            this._getData();
        },
        methods: {
            formatMoneyAddComma,
            parseTime,
            _getData() {
                this.heads = Array.from({
                    length: 12
                }).map((k, index) => index + 1);
                ['xf', 'kl'].map((org, index) => {
                    getTzQuery({
                        month: parseTime(this.month, '{y}-{m}'),
                        org,
                    }).then(res => {
                        this._sum(res.data, index);
                    })
                })
            },
            _sum(target, k) {
                let everyMonthSum = Array.from(new Array(12)).map(_ => 0);
                target.forEach(item => {
                    item.sum = item.data.reduce((a, b) => a + b);
                    item.data.forEach((m, index) => {
                        if (!everyMonthSum[index]) everyMonthSum[index] = 0;
                        everyMonthSum[index] += Number(m);
                    })
                });
                this.everyMonthSum[k] = everyMonthSum;
                this.allSum[k] = everyMonthSum.reduce((a, b) => a + b)
                this.$set(this.list, k, target);
            },
            //查询为确认账单情况
            _getNoConfirm() {
                getJsNoConfirm({
                    month: parseTime(this.month, '{y}-{m}'),
                }).then(res => {
                    this.infoIems = [{
                        label: '当前账单总数',
                        num: res.data.countAll,
                    }, {
                        label: '未确认账单数',
                        num: res.data.count,
                    }, {
                        label: '已确认账单数',
                        num: res.data.countAll - res.data.count,
                    }]
                })
            },
            //下载excel
            html2Excel(k) {
                new Table2Excel(`#table-content${k?k:''} table`).export(
                    `${this.tableTitle[k]}-${this.parseTime(new Date(),'{y}-{m}-{d} {h}-{i}-{s}')}`);
            },
            //刷新数据
            reloadTable() {
                this._getData();
            }
        },
        activated: function () {
            // 移除任何你不想保留的数据
            this._getData();
        },

    }
</script>
<style scoped lang="scss">
    @media print {

        #table-content,
        #table-content2 {
            zoom: 85%;
            margin-top: 0 !important;
        }

        table {
            page-break-inside: avoid;
        }

        .head {
            font-size: 18px;
            font-weight: 600;
        }

        .scorll {
            overflow: hidden;
            padding-right: 2px;

        }


        .desc-icon,
        .desc-input {
            display: none !important;
        }

        .desc-print {
            display: inline-block !important;
        }

        .table-h {
            color: #000;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        th,
        span {
            font-size: 9pt !important;
            padding: 40px 0px !important;
        }

        th,
        td {
            border: .1pt solid !important;
        }

        td,
        span {
            color: #000;
        }

        .table-footer,
        .table-h-list,
        .table-h {
            line-height: 0.3in !important;
        }

        .left {
            margin-left: 3in !important;
        }

        .right {
            margin-right: 2in !important;
        }
    }

    #table-content,
    #table-content2 {
        margin-top: 24px;
    }

    .month-div {
        position: absolute;
        top: 12px;
        right: 24px;
        z-index: 2;

        /deep/ .el-date-editor {
            width: 1.4rem;
        }
    }


    .scorll {
        padding-bottom: 10px;
        padding-right: 2px;
    }

    .noBorder {
        td {
            border: 0 !important;
        }

        &:hover {
            background-color: transparent !important;
        }
    }

    .box {
        padding-top: 24px;
        position: relative;

        .print-btn {
            position: absolute;
            right: 0;
            top: 0;
        }

        .desc-print {
            display: none;
        }

        .desc-input {
            border: 0;
            outline: none;
            color: #303133;
            font-size: 14px;
            font-family: "PFM" !important;
        }
    }

    th,
    td {
        border: 1px solid #d3d3d3;
    }

    th,
    td {
        text-align: center;
        padding: 10px 5px;
        white-space: nowrap;
    }

    .table {
        border-collapse: collapse;
        width: 100%;
    }

    .item {
        margin-bottom: 16px;

        .table-h {
            font-size: 16px;
            text-align: center;
            line-height: 40px;

        }

        .table-h-list {
            line-height: 40px;
            display: flex;
            justify-content: space-between;
            font-size: 14px;

            span {
                text-align: left;

                &:last-child {
                    text-align: right;
                }
            }
        }

        .table-footer {
            display: flex;
            justify-content: space-between;
            line-height: 40px;

            .left {
                margin-left: 150px;
            }

            .right {
                margin-right: 100px;
            }
        }
    }
</style>